@import url(https://fonts.googleapis.com/css?family=Gilda+Display);
header {
  min-height: 70px;
  width: 100%;
  overflow: hidden;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
  border-bottom: 1px solid #ddd;
  background-color: #585857;
  padding-left: 40px;
}
header .logo {
  flex: 1 1;
}
header a {
  text-transform: uppercase;
  color: rgb(255, 255, 255);
}
header ul li {
  display: inline-block;
  opacity: 0.7;
  padding: 0 20px;
  color: #ffffff;
}
header ul li:hover {
  opacity: 1;
  color: #ffffff;
}
.menu {
  display: none;
}
.cart-icon {
  position: relative;
  margin-right: 40px;
}
.cart-icon span {
  background: rgb(153, 152, 152);
  border-radius: 10px;
  color: white;
  position: absolute;
  top: -10px;
  right: -10px;
  padding: 5px 7px;
  font-size: 10px;
}

.product_card {
  max-width: 300px;
  overflow: hidden;
  height: 450px;
  padding: 15px;
  box-shadow: 0 0 15px rgb(201, 201, 201);
  margin: 10px 0;
  position: relative;
  border-radius: 3px;
}
.product_card img {
  width: 100%;
  height: 180px;
  display: block;
  object-fit: contain;
}
.product_box h3 {
  width: 100%;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  text-transform: capitalize;
  cursor: pointer;
  color: #323232;
}
.product_card span {
  color: rgb(103, 101, 226);
}
.product_box p {
  width: 100%;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  height: 150px;
  overflow: hidden;
  color: #323232;
}
.row_btn {
  width: 100%;
  margin-top: 10px;
  display: flex;
  justify-content: space-between;
}
.row_btn a {
  width: 50%;
  text-align: center;
  text-transform: uppercase;
  color: white;
  font-weight: 600;
  letter-spacing: 2px;
  padding: 6px;
}
#btn_buy {
  background: rgb(194, 47, 47);
  margin-right: 5px;
  border-radius: 15px;
}
#btn_view {
  background: #7d9ee7;
  margin-left: 5px;
  border-radius: 15px;
}
.product_card input {
  position: absolute;
  width: 25px;
  height: 25px;
}

.products {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  justify-items: center;
  margin: 20px 0;
}

/* ----------- Product Item ----------------- */

.delete-all {
  text-align: right;
  margin: 20px;
}
.delete-all input {
  height: 25px;
  width: 25px;
  transform: translateY(5px);
  margin: 0 15px;
}
.delete-all span {
  text-transform: uppercase;
  color: rgb(3, 165, 206);
  letter-spacing: 1.3px;
}
.delete-all button {
  background: crimson;
  border-radius: 3px;
  border-bottom: 1px solid crimson;
  padding: 10px 25px;
  color: rgb(255, 255, 255);
  text-transform: uppercase;
}

/* ------------ Filters Menu ----------------- */
.filter_menu {
  width: 97%;
  min-height: 40px;
  overflow: hidden;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  margin: 15px 0;
  margin-left: 15px;
}
.filter_menu select,
input {
  border: 1px solid rgb(36, 184, 230);
  border-radius: 3px;
  outline: none;
  height: 40px;
  padding: 0 5px;
}
.filter_menu input {
  flex: 1 1;
  margin: 0 10px;
}

/* -------------------- Load More --------------- */
.load_more {
  text-align: center;
}

.detail {
  width: 100%;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  padding: 50px;
  font-size: 150%;
}
.detail img {
  max-width: 400px;
  width: 100%;
  margin: -10px;
  height: 100%;
  display: block;
  object-fit: contain;
}
.box-detail {
  max-width: 500px;
  width: 100%;
  margin: 5px 20px;
}
.box-detail .row {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.box-detail h3 {
  text-transform: uppercase;
  color: darkblue;
  letter-spacing: 2px;
  font-weight: 2rem;
}
.box-detail span {
  line-height: 1.5;
  margin: 10px 0;
  font-size: 20px;
  opacity: 0.8;
  color: darkblue;
}
.box-detail p {
  line-height: 1.5;
  margin: 20px 0;
  font-size: 16px;
  opacity: 0.8;
}
.box-detail .cart {
  background: rgb(53, 110, 233);
  border-radius: 3px;
  border-bottom: 1px solid rgb(53, 110, 233);
  color: white;
  margin-top: 10px;
  padding: 3px 5px;
  display: inline-block;
}

.Related {
  text-transform: uppercase;
  color: rgb(20, 87, 211);
  text-align: center;
  letter-spacing: 2px;
  font-weight: 2rem;
}

.login-page {
  max-width: 500px;
  border: 1px solid #4c4d4c;
  border-radius: 5px;
  padding: 30px;
  margin: 50px auto;
}
.login-page h2 {
  text-transform: uppercase;
  letter-spacing: 2px;
  color: rgb(53, 110, 233);
}
.login-page form input,
.login-page form button {
  border-radius: 5px;
  width: 100%;
  height: 40px;
  margin: 10px 0;
  padding: 0 5px;
  outline: #4c4d4c;
  border: 1px solid #4c4d4c;
}
.login-page form .row {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.login-page form button {
  border-radius: 5px;
  width: 150px;
  background: #67a2e6;
  color: rgb(255, 255, 255);
  border: 1px solid #ffffff;
  text-transform: uppercase;
  letter-spacing: 2px;
}
.login-page form a {
  color: rgb(228, 112, 18);
  letter-spacing: 1.3px;
  text-transform: uppercase;
}

.cart {
  position: relative;
  border: 1px solid #ccc;
  transform: scaleY(0.98);
}
.cart-image {
  width: 100%;
  height: 480px;
  object-fit: contain;
}
.amount span {
  color: rgb(95, 142, 228);
  padding: 0 20px;
}
.amount button {
  width: 20px;
  height: 20px;
  border: 1px solid #777;
}
.delete {
  position: absolute;
  top: 0;
  right: 5px;
  color: crimson;
  font-weight: 900;
  cursor: pointer;
}
.total {
  width: 100%;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.total h3 {
  color: rgb(92, 126, 219);
}

.history-page{
    overflow-x: auto;
}
.history-page h2, h4{
    text-align: center;
    margin: 20px;
    text-transform: uppercase;
    letter-spacing: 1.2px;
}
.history-page table{
    margin: auto;
    width: 100%;
}
.history-page table,th,tr,td{
    border: 1px solid #ddd;
    border-collapse: collapse;
}
th, td{
    text-align: center;
    padding: 10px;
    text-transform: capitalize;
}
table a{
    color: rgb(3, 165, 206);
}

.history-page img{
    width: 70px;
    height: 100px;
    object-fit: cover;
}
.categories {
  max-width: 700px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  margin: 40px auto;
}
.categories form {
  width: 290px;
  margin-bottom: 20px;
}
.categories label {
  display: block;
  font-weight: 700;
  letter-spacing: 2px;
  text-transform: uppercase;
  margin-bottom: 10px;
}
.categories input,
button {
  height: 35px;
  border: none;
  outline: none;
  border-bottom: 1px solid rgb(88, 88, 90);
}
.categories input {
  width: 210px;
}
.categories button {
  width: 70px;
  background: rgb(53, 110, 233);
  border-radius: 3px;
  border-bottom: 1px solid rgb(53, 110, 233);
  color: white;
  margin-left: 10px;
}
.categories .row {
  min-width: 290px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  border-radius: 5px;
  margin-bottom: 10px;
  border: 1px solid rgb(168, 168, 168);
}

.create_product {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-around;
}
.upload {
  max-width: 450px;
  height: 500px;
  width: 100%;
  border: 1px solid #ddd;
  padding: 15px;
  margin: 20px;
  position: relative;
}
#file_up {
  position: relative;
  width: 100%;
  height: 100%;
  outline: none;
}
#file_up::before {
  content: 'Click Here';
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: white;
  color: rgb(153, 153, 153);
  font-size: 5rem;
  text-align: center;
  cursor: pointer;
  margin: auto;
}
#file_img {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background: white;
}
#file_img img {
  width: 100%;
  height: 100%;
  display: block;
  object-fit: contain;
}
#file_img span {
  position: absolute;
  top: -13px;
  right: -13px;
  background: white;
  border: 1px solid #ddd;
  border-radius: 50%;
  padding: 6px 10px;
  cursor: pointer;
  font-weight: 900;
  color: crimson;
}

.create_product form {
  max-width: 500px;
  min-width: 290px;
  width: 100%;
  margin: 15px 30px;
}
.create_product form .row {
  width: 100%;
  margin: 15px 0;
}
.create_product form input,
textarea {
  width: 100%;
  min-height: 40px;
  border-radius: 3px;
  padding: 0 5px;
}
.create_product form button {
  width: 200px;
  height: 40px;
  background: rgb(53, 110, 233);
  border-radius: 3px;
  border-bottom: 1px solid rgb(53, 110, 233);
  color: white;
  text-transform: uppercase;
  letter-spacing: 2px;
  font-weight: 700;
}

* {
  padding: 0;
  box-sizing: border-box;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.App {
  max-width: 1370px;
  min-height: 100vh;
  margin: 0 auto;
  padding: 0px;
  box-shadow: 0 0 35px #eee;
}
a {
  text-decoration: none;
}
ul li {
  list-style: none;
}
button {
  border: none;
  outline: none;
  background: transparent;
  cursor: pointer;
}

