.categories {
  max-width: 700px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  margin: 40px auto;
}
.categories form {
  width: 290px;
  margin-bottom: 20px;
}
.categories label {
  display: block;
  font-weight: 700;
  letter-spacing: 2px;
  text-transform: uppercase;
  margin-bottom: 10px;
}
.categories input,
button {
  height: 35px;
  border: none;
  outline: none;
  border-bottom: 1px solid rgb(88, 88, 90);
}
.categories input {
  width: 210px;
}
.categories button {
  width: 70px;
  background: rgb(53, 110, 233);
  border-radius: 3px;
  border-bottom: 1px solid rgb(53, 110, 233);
  color: white;
  margin-left: 10px;
}
.categories .row {
  min-width: 290px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  border-radius: 5px;
  margin-bottom: 10px;
  border: 1px solid rgb(168, 168, 168);
}
