.cart {
  position: relative;
  border: 1px solid #ccc;
  transform: scaleY(0.98);
}
.cart-image {
  width: 100%;
  height: 480px;
  object-fit: contain;
}
.amount span {
  color: rgb(95, 142, 228);
  padding: 0 20px;
}
.amount button {
  width: 20px;
  height: 20px;
  border: 1px solid #777;
}
.delete {
  position: absolute;
  top: 0;
  right: 5px;
  color: crimson;
  font-weight: 900;
  cursor: pointer;
}
.total {
  width: 100%;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.total h3 {
  color: rgb(92, 126, 219);
}
